/* Footer.css */

.footer {
    background-color: #203040;
    color: #f5f5f5;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
  }
  
  .footer p {
    margin: 0;
  }
  
  .footer a {
    color: #f5f5f5;
    text-decoration: none;
    margin: 0 10px;
  }
  
  .footer a:hover {
    color: #fff;
  }
  